<template>
  <div class="wrapper">
  		
    <div style='position: fixed;top: 0;background-color: #20282B;width: 750px;height: 100px;'>
		  <text style="font-size: 36px;color: #ffffff;" class="middle">autel</text>
		  <text class='back' @click='back'>{{$route.path==='/'?'':'<'}}</text>
		  <text class="right" @click="showPop" :class='comStyle'>+</text>
		</div>
		<wxc-popover 
	   		ref="wxc-popover"
			 :buttons="btns"
			 :position="popoverPosition"
			 :arrowPosition="popoverArrowPosition"
			 @wxcPopoverButtonClicked="popoverButtonClicked"
		 ></wxc-popover>
		 <router-view/>
  </div>
</template>

<script>
	var modal = weex.requireModule('modal')
	import { WxcTabBar, Utils,WxcMinibar,WxcIcon,WxcPopover   } from 'weex-ui';
	import { RETURN_ICON, SCAN_ICON, QRCODE_ICON, QUESTION_ICON } from './components/types';
export default {
  name: 'App',
  data () {
    return {
    	slideName:'slide-left',
      logo: 'https://gw.alicdn.com/tfs/TB1yopEdgoQMeJjy1XaXXcSsFXa-640-302.png',
      btns:[
        {
         icon: SCAN_ICON,
         text:'Scan',
         key:'key-scan'
        },
        {
          icon: QRCODE_ICON,
          text:'My Qrcode',
          key:'key-qrcode'
        },
        {
          icon: QUESTION_ICON,
          text:'Help',
          key:'key-help'
        },
      ],
      popoverPosition:{x:-14,y:90},
      popoverArrowPosition:{pos:'top',x:-15},
    }
  },
  created(){
  	console.log(this.$route.path)
  },
  components:{WxcMinibar,WxcPopover,WxcIcon},
  computed:{
  	comStyle(){
  		if(weex.config.env.platform==='Web'){
  			return 'webClass'
  		}
  	}
  },
  methods:{
  	 showPop() {
        this.$refs['wxc-popover'].wxcPopoverShow();
      },
      popoverButtonClicked(obj){
      	 modal.toast({ 'message': `key:${obj.key}, index:${obj.index}`, 'duration': 1 });
      },
      back(){
      	if(this.$route.path!=='/'){
      		this.$router.go(-1)
      	}
      	
      }
      
  }
}
</script>

<style>
  .wrapper {
    justify-content: center;
    align-items: center;
    overflow: auto;
  }
  .middle{
  	text-align: center;
  	line-height: 100px;
  }
  
  .right{
  	padding-left: 30px;
  	padding-right: 30px;
  	position: absolute;
  	right: 2px;
  	top: 0;
  	font-size: 60px;
  	color: #ffffff;
  	line-height: 100px;
  }
  
  .webClass{
  	padding-left: 30px !important;
  	padding-right: 30px !important;
  }
  
  .back{
  	position: absolute;
  	left: 30px;
  	top: 0;
  	color: #ffffff;
  	font-size: 50px;
  	line-height: 100px;
  }
</style>



