<template>
	<div class="list">
		<!--<div class='colum1'>
			<text class='txt'>{{list.type}}</text>
		</div>
		<div class='colum2'>
			<text class='txt'>{{list.issue?'Issues':'Condition'}}</text>
			<text :class="[list.issue?'light':'icon']">&#xe63c;</text>
		</div>
		<div class="colum3">
			<text class='txt'>{{list.hood}}</text>
		</div>-->
		<div class="colum4">
			<!--<text class='icon'>&#xe600;</text>-->
			<text class='icon btn' @click='report()'></text>
			<!--<text class='icon' @click="recoder(index)">&#xe64f;</text>-->
			<text class='icon btn' @click="recoder()">{{comTxtRec}}</text>
			<!--<text class='icon' @click="picture(index)">&#xe603;</text>-->
			<text class='icon btn' @click="picture()">{{pathPic?'':''}}</text>
			
			<text class='icon btn' @click='video()'>{{pathVdo?'':''}}</text>
			
			<text class='icon btn' @click='light()'></text>
			
			<text class='icon btn' @click='screen()'></text>
		</div>
		
	</div>
</template>

<script>
	import Nat from 'natjs'
	export default{
		data(){
			return{
				pathRec:'',
				pathPic:'',
				pathVdo:'',
				recStatus:false,
			}
		},
		props:{
			list:{
				type:Object
			}
		},
		computed:{
			comTxtRec(){
				if(this.pathRec){
					return ""
				}else{
					if(!this.recStatus){
						return ''
					}else{
						return ''
					}
				}
			}
		},
		created(){
			this.initIconFont()
		},
		methods:{
			initIconFont () {
		      let domModule = weex.requireModule('dom')
		      domModule.addRule('fontFace', {
		        'fontFamily': 'iconfont',
		        'src': "url('http://at.alicdn.com/t/font_886417_j1z3d85q24s.ttf')"
		      })
		   },
		   recoder(){
		   	if(!this.pathRec){
		   		if(!this.recStatus){
			   		Nat.recorder.start({
					}, () => {
						this.recStatus = true
					    Nat.toast('start')
					})
			   	}else{
			   		Nat.recorder.stop((err, ret) => {
						if (err) {
							Nat.toast('[ERROR] ' + JSON.stringify(err))
							return
						}
						Nat.toast('stopped, path: ' + ret.pathRec)
						this.pathRec = ret.path
						this.recStatus = false
					})
			   	}
		   	}else{
				Nat.video.play(this.pathRec)
		   	}
		   	
		   	 
		  },
		  picture(){
		  	if(!this.pathPic){
		  		Nat.image.pick({
				    showCamera: true,
				}, (err, ret) => {
				    this.pathPic = ret.paths
				})
		  	}else{
		  		Nat.image.preview([
				'http://cdn.instapp.io/nat/samples/bnw.jpeg',
				'http://cdn.instapp.io/nat/samples/fibonacci.jpeg',
				].concat(this.pathPic))
		  	}
		 },
		 video(){
		 	if(!this.pathVdo){
		 		Nat.camera.captureVideo({}, (err, ret) => {
					if (err) {
						Nat.toast('[ERROR] ' + JSON.stringify(err))
						return
					}
					Nat.toast(JSON.stringify(ret, null, 2))
					this.pathVdo = ret.path
				})
		 	}else{
		 		Nat.video.play(this.pathVdo)
		 	}
		 },
		 light(){
		 	Nat.screen.brightness.set(0.2)
		 },
		 report(){
		 	this.$router.push('/report')
		 },
		 screen(){
		 	Nat.screen.info((err, ret) => {
			   Nat.alert(JSON.stringify(ret))
			})
		 }
		

		}
	}
</script>

<style scoped>
	.list{
		width: 750px;
		height: 100px;
		border-bottom-width: 1px;
		border-bottom-style: solid;
		border-bottom-color: #cccccc;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		padding-left: 20px;
	}
	
	.colum1{
		display: flex;
		flex-direction: row;
		align-items: center;
		flex: 3;
	}
	.colum2{
		display: flex;
		flex-direction: row;
		align-items: center;
		flex: 3;
	}
	.colum3{
		display: flex;
		flex-direction: row;
		align-items: center;
		flex: 2;
	}
	.colum4{
		display: flex;
		flex-direction: row;
		align-items: center;
		flex: 5;
	}
	.txt{
		font-size: 24px;
	}
	
	.icon {
	   font-family: iconfont;
	   margin-left: 10px;
	   font-size: 24px;
	}
	.btn{
		/*width: 100px;*/
		padding-left: 10px;
		padding-right: 10px;
		height: 60px;
		line-height: 60px;
		border-radius: 6px;
		text-align: center;
		background-color: #0088FB;
		color: #fff;
	}
	.light{
		color: lightgreen;
		font-family: iconfont;
	    margin-left: 10px;
	}
</style>


