import Vue from 'vue'
/*global Vue*/
import Router from 'vue-router'
import Main from '@/components/main'
import appMain from '@/pages/appMain'
import report from '@/pages/report'
Vue.use(Router)

module.exports = new Router({
  routes: [
    {
      path: '/',
      name: 'appMain',
      component: appMain
    },
    {
      path: '/report',
      name: 'report',
      component: report
    }
  ]
})



